* {
  box-sizing:border-box;
  margin:0;
  padding:0;
  font-family:Arial, Helvetica, sans-serif;
  background-color: #fff;
  color: #1a1a1c;
}

.coin-app{
  display:flex;
  flex-direction: column;
  align-items:center;
  margin-top:64px;
  margin-bottom: 100px;
  color: #fff;
}

.coin-search {
  margin-bottom: 64px;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;
}

.coin-input {
  padding-left: 16px;
  width:500px;
  height:60px;
  border-radius:4px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
  );
  color: #e2e2e2;
}

.coin-input::placeholder {
  color: #e2e2e2;
}